import React from 'react'
import courses from '../data.json'
import CourseList from '../component/course-list/read'
import CourseListEdit from '../component/course-list/edit'
import withEdit from '../hooks/withEdit.jsx'

export default withEdit(props =>
  !props.edit ? (
    <CourseList courses={courses} {...props} />
  ) : (
    <CourseListEdit courses={courses} {...props} />
  )
)
