import IconButton from '@material-ui/core/IconButton'
import { makeStyles } from '@material-ui/core/styles'
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward'
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward'
import DeleteIcon from '@material-ui/icons/Delete'
import { FieldArray, Form, Formik } from 'formik'
import React from 'react'
import * as Yup from 'yup'
import validationSchema from '../../utils/validation'
import CourseListRead from './read'
import Button from '@material-ui/core/Button'

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper
  },
  listItem: {
    borderBottomColor: theme.palette.grey[400],
    borderBottomWidth: 1,
    borderBottomStyle: 'solid'
  },
  inline: {
    display: 'inline'
  }
}))

const schema = Yup.object().shape({
  courses: Yup.array().of(
    Yup.object().shape({
      name: validationSchema.course.name,
      image: validationSchema.course.image
    })
  )
})

export default function CourseList({ courses, edit }) {
  const swipUp = (courses, index, setFieldValue) => {
    if (index > 0) {
      setFieldValue(`courses[${index}].order`, courses[index - 1].order)
      setFieldValue(`courses[${index - 1}].order`, courses[index].order)
    }
  }

  const swipDown = (courses, index, setFieldValue) => {
    if (index < courses.length - 1) {
      setFieldValue(`courses[${index}].order`, courses[index + 1].order)
      setFieldValue(`courses[${index + 1}].order`, courses[index].order)
    }
  }

  const deleteCourse = (courses, arrayHelpers, index) => {
    arrayHelpers.remove(index)
  }

  const ActionButton = ({ values, index, setFieldValue, arrayHelpers }) => (
    <>
      <IconButton size='small' onClick={() => swipDown(values.courses, index, setFieldValue)}>
        <ArrowDownwardIcon fontSize='inherit' />
      </IconButton>
      <IconButton size='small' onClick={() => swipUp(values.courses, index, setFieldValue)}>
        <ArrowUpwardIcon fontSize='inherit' />
      </IconButton>
      <IconButton size='small' onClick={() => deleteCourse(values.courses, arrayHelpers, index)}>
        <DeleteIcon fontSize='inherit' />
      </IconButton>
    </>
  )

  const FormContent = props => (
    <>
      <CourseListRead
        courses={props.values.courses}
        renderCourseAction={(course, index) => <ActionButton index={index} {...props} />}
      />
      <Button
        variant='contained'
        color='secondary'
        onClick={() =>
          props.arrayHelpers.push({
            id: courses.length + 1,
            name: '',
            image: '',
            order: courses.length
          })
        }
      >
        Add a course
      </Button>
    </>
  )

  return (
    <Formik
      initialValues={{ courses: courses }}
      onSubmit={values => alert(JSON.stringify(values, null, 2))}
      validationSchema={schema}
      render={({ submitForm, ...props }) => (
        <Form>
          <FieldArray
            name='courses'
            render={arrayHelpers => <FormContent arrayHelpers={arrayHelpers} {...props} />}
          />
          <Button
            variant='contained'
            color='primary'
            onClick={() => {
              debugger
              submitForm()
            }}
          >
            Submit
          </Button>
        </Form>
      )}
    ></Formik>
  )
}
